import { CacheProvider } from './../../../providers/cache-provider';
import { PreloadProvider } from './../../../providers/preload-provider';
import { Utility } from './../../../services/utility';
import { StorageService } from './../../../services/storage-service';
import { Component, ViewChild, ChangeDetectorRef, ElementRef } from '@angular/core';
import { TranslateService, TranslationChangeEvent } from '@ngx-translate/core';
import { IonicPage, NavController, Tabs, Events } from 'ionic-angular';

import { JPushService } from '../../../services/services';
import { Tab1Root, Tab2Root, Tab3Root, Tab4Root, Tab5Root, Tab6Root } from '../../pages';

import { MessageProvider } from '../../../providers/providers';
import { Session } from '../../../services/session';
import { STORAGE_KEY } from '../../../services/constants';

@IonicPage()
@Component({
  selector: 'page-tabs',
  templateUrl: 'tabs.html'
})
export class TabsPage {
  @ViewChild('mainTabs')
  tabRef: Tabs;

  tab1Root: any = Tab1Root;
  tab2Root: any = Tab2Root;
  tab3Root: any = Tab3Root;
  tab4Root: any = Tab4Root;
  tab5Root: any = Tab5Root;
  tab1Title = " ";
  tab2Title = " ";
  tab3Title = " ";
  tab4Title = " ";
  tab5Title = " ";

  // isEmployee: Boolean = false;
  isShowScreenShot: boolean = false
  showSocial: boolean = false;
  newMessageCount: number = 0;
  showTabs: boolean = true;

  constructor(public navCtrl: NavController,
    public translateService: TranslateService,

    public messageProvider: MessageProvider,
    public session: Session,
    public events: Events,
    public jpushService: JPushService,
    public storageService: StorageService,
    public cd: ChangeDetectorRef,
    public element: ElementRef,
    public preloadProvider: PreloadProvider,
    public cacheProvider: CacheProvider) {

    this._setTabText();
    this.init(); 
    // 当收到推送消息时（此时将消息存储）
    this.events.subscribe('jpush.notificationReceived', () => {
      // if (this.tabRef.getSelected().index != 1) {
      // this.newMessageCount += 1;
      this._setTabMessageBadge();
      // }
    });

    this.events.subscribe('beta: showScreenShotButton', (isShowScreenShot) => {
      this.isShowScreenShot = isShowScreenShot;
    })
    this.events.subscribe('lang:changed', (event) => {
      this._setTabText();
    });

    this.events.subscribe('to-homemain', (event) => {
      this.selectTab(0)
    })

    this.events.subscribe('changeTabsDisplay', (isHide) => {  
      this.showTabs = !isHide
    })

    
    this.events.subscribe('user:info-changed', () => {
      // this.isEmployee = this.session.isEmployee;
      setTimeout(() => {
        this.init()
        this.cd && this.cd.detectChanges();
      }, 100)
    })

    this.events.subscribe('message:count-changed', (count) => {
      if (count > 0) {
        this.newMessageCount = count;
      } else {
        this.newMessageCount = 0;
      }
      this.jpushService.setIosIconBadgeNumber(count);
      this._setTabMessageBadge();
      // setTimeout(()=>{ 
      //   if (!this.cd['destroyed'])this.cd.detectChanges();
      // },500) 
    })

    this.events.subscribe('MassFlagChange', () => {
      this.init()
    })
  }

  ionChange() {
    if (this.tabRef.getSelected().index == 1) {

    }
  };

  selectTab(index) {
    this.tabRef.select(index);
    this.ionChange();
  }
  _setTabMessageBadge() {
    if (this.newMessageCount == 0) {
      this.tabRef.getByIndex(1).tabBadge = "";
    } else {
      this.tabRef.getByIndex(1).tabBadge = this.newMessageCount.toString();
      this.cd && this.cd.detectChanges();
    }
  }

  _setTabText() {
    this.translateService.get(['TABS_TAB1_TITLE', 'TABS_TAB2_TITLE', 'TABS_TAB3_TITLE', 'TABS_TAB4_TITLE', 'TABS_TAB5_TITLE']).subscribe(values => {
      this.tab1Title = values['TABS_TAB1_TITLE'];
      this.tab2Title = values['TABS_TAB2_TITLE'];
      this.tab3Title = values['TABS_TAB3_TITLE'];
      this.tab4Title = values['TABS_TAB4_TITLE'];
      this.tab5Title = values['TABS_TAB5_TITLE'];
    });
  }
  ngOnInit() {
    this.translateService.onLangChange.subscribe((event: TranslationChangeEvent) => {
      this._setTabText();
    });



  }

  async init() { 
    try{
      let res = await this.cacheProvider.getSocialSetting()  
      this.showSocial = res.showSocial
      this.session.showSocial = res.showSocial
    }catch(e){ } 
    this.cd && this.cd.detectChanges();
  }

  screenShot() {
    Utility.captureWebPage(this.element.nativeElement).subscribe(res => {
      this.preloadProvider.previewImage([
        { pictureUrl: res.result }
      ], 0)
    })
  }
}



