import { HandleProvider } from './../../../providers/handle/handle';
import { ContentProvider } from './../../../providers/content-provider';
import { Logger } from './../../../services/logger';
import { PreloadProvider } from './../../../providers/preload-provider';
import { IDLRecruitingProvider } from './../../../providers/idl-recruiting-provider';
import { UserProvider } from './../../../providers/user-provider';
import { Utility } from './../../../services/utility';
import { ServerProvider } from './../../../providers/server-provider';
import { NativeService } from './../../../services/native-service';

import { Component, ViewChild, HostListener, ElementRef } from '@angular/core';
import { IonicPage, NavController, Slides, Events, Content } from 'ionic-angular';
import { Widget } from '../../../models/widget';
import { TabsPage } from '../../common/tabs/tabs';
import { AuthProvider, WidgetProvider } from '../../../providers/providers';
import { Session, StorageService } from '../../../services/services';
import { TranslateService } from '@ngx-translate/core';
import { Webpage } from '../../../models/models';
import { DragulaService } from 'ng2-dragula';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home-main.html'
})
export class HomePage {
  cardItems: any[];
  favoriteWidgets: any[];
  friendlyLinkList: any[];
  hotNews: any[];
  sign: {
    isNeedSign: Boolean,
    lastSignTime: string
  };
  numbers: number = 1;
  cnt: number;
  autoplay: number = 3000;
  initX: number = 300;//悬浮球初始横坐标
  initY: number = 300;//悬浮球初始纵坐标
  touchTarget: any;
  srcollable: boolean = false; //是否可滑动
  fabdom: any; //悬浮球DOM
  uniqueBagId: string = 'ion-fab' //悬浮球 dragula bagid
  isShowFabButton: boolean = true;  //是否显示悬浮球
  isEmployee: Boolean = true;
  stableButtons: any[];
  previewJobs: any;
  isLoadingNews: boolean = false;
  newsList: any[];
  slideTimer: any; 
  @ViewChild(Slides) slides: Slides;
  @ViewChild(Content) content: Content;
  @ViewChild("header") header;
  @ViewChild("scroll") scrollx;
  
  constructor(public navCtrl: NavController,
    public authProvider: AuthProvider,
    public widgetProvider: WidgetProvider,
    public session: Session,
    public storageService: StorageService,
    public events: Events,
    public translateService: TranslateService,
    public tabRef: TabsPage,
    private dragulaService: DragulaService,
    private nativeService: NativeService,
    public element: ElementRef,
    private serverProvider: ServerProvider, 
    public idlrecruitingProvider: IDLRecruitingProvider, 
    public logger: Logger,
    public contentProvider: ContentProvider,
    public handleProvider: HandleProvider
  ) {
    this.initVariable() //初始化变量
    this.initHomeData() //初始主页数据
    this.getHistoryLocation() //初始化客服图标位置
    this.initDragulaService()
    this.initEventSubscribe();
  }
  
  initEventSubscribe() {
    this.events.subscribe('user:logined', (time) => { //用户登录成功事件监听
 
      this.nativeService.splashScreenHide();
      if (this.session.homedata) {
        this.storageService.setItem('homedata', this.session.homedata)
        this.storageService.setItem('isPunchMember', this.session.isPunchMember)

        // this.initPostList(this.session.homedata['widgets'])

        
        if (!Utility.isEqualsObject(this.hotNews, this.session.homedata['hotNews'])) {
          this.hotNews = this.session.homedata['hotNews'];
        }
        if (!Utility.isEqualsObject(this.friendlyLinkList, this.session.homedata['friendlyLinks'])) {
          this.friendlyLinkList = this.session.homedata['friendlyLinks'];
        }
        if (!Utility.isEqualsObject(this.cardItems, this.session.homedata['slides'])) {
          this.cardItems = this.session.homedata['slides'];
        }
        if (!Utility.isEqualsObject(this.favoriteWidgets, this.session.homedata['widgets'])) {
          this.favoriteWidgets = this.session.homedata['widgets'];
        }
        if (this.sign.isNeedSign != this.session.isPunchMember) {
          this.sign.isNeedSign = this.session.isPunchMember
        }

        
      this.loadNewsList();
      } else {
        this._setInif();
      }

      this.resizeSlides()
    });

    //变更语言更新
    this.events.subscribe('lang:changed', (event) => {
      if (!event.isNotRefresh) { //是否需要刷新语言
        this._setInif(); 
      }
    });
    this.events.subscribe('Home:subscribe', (time) => { //应用变更事件监听
      this.widgetProvider.getFavorites().subscribe((data: any) => {
        if (data.errcode == 0) {
          this.favoriteWidgets = data.result;
        }
      }, err => {
        this.logger.log(JSON.stringify(err), 'Home:subscribe')
      });
    });

    this.events.subscribe('user:permission-changed', () => { //用户权限变更事件监听
      this._setInif();
    });

    this.events.subscribe('user:info-changed', () => {
      this.isEmployee = this.session.isEmployee; 
    })

    this.events.subscribe('register:success', () => {
      this.isEmployee = this.session.isEmployee;
      if(this.session.user && !this.session.user.hasPassword){
        this.navCtrl.push('SetPasswordPage');
      }
    })
     

    this.events.subscribe('previewJobs', () => {
      // for (let item of this.session.widget) {
      //   // this.initPostList(item.widgets);
      // }
    })

    this.events.subscribe('homemain:resize-content',()=>{
      this.content.resize();
    })

    this.events.subscribe('setting:change-theme',value=>{
      setTimeout(()=>{ 
        if(this.content && this.content.scrollTop){
          this.setHeaderBackground(this.content.scrollTop)
        }
      },300)
    })
  }

  canRefresh(){
    return !window['scrollingX'];
  }
  _setInif() {
    this.widgetProvider.getHomeData().subscribe((data: any) => { //获取首页数据
      if (data.errcode == 0) {


        // this.initPostList(data.result.widgets);
        if (!Utility.isEqualsObject(this.hotNews, data.result.hotNews)) {
          this.hotNews = data.result.hotNews;
        }
        if (!Utility.isEqualsObject(this.friendlyLinkList, data.result.friendlyLinks)) {
          this.friendlyLinkList = data.result.friendlyLinks;
        }
        if (!Utility.isEqualsObject(this.cardItems, data.result.slides)) { 
          this.cardItems = data.result.slides; 
        }
        if (!Utility.isEqualsObject(this.favoriteWidgets, data.result.widgets)) { 
          this.favoriteWidgets = data.result.widgets;
        }

        this.storageService.setItem("homedata", data.result);
        this.resizeSlides()
         
      }
    }, err => {
      this.logger.log(JSON.stringify(err), 'HomePage.widgetProvider.getHomeData()')
    });

    // this.userProvider.IsPunchMember().subscribe((res) => { //获取用户是否需要punching
    //   if (res.errcode === 0) {
    //     this.session.isPunchMember = res.result.isPunchMember;
    //     this.sign.isNeedSign = res.result.isPunchMember;
    //     this.storageService.setItem('isPunchMember', this.session.isPunchMember) 
    //   }

    // });
    // this.preloadProvider.getAllWidget(false);
 
    this.loadNewsList();
    
  }


  /**
   * 下拉刷新
   * @param refresh 刷新事件
   */
  doRefresh(refresh) {
    if (this.nativeService.isConnecting()) {
      this._setInif();

      
    }
    setTimeout(() => {
      refresh.complete()
    }, 500);

  }

  /**
   * 横向滚动条宽度
   */
  scrollWidth(){   
    return {
      "width": (window.screen.availWidth - 5)   + 'px',
      "height": 200 + 'px',
    }
  }
  ionViewDidLoad() {
    this.fabdom = this.element.nativeElement.querySelector('#homemainfab');
    if (this.nativeService.isIos()) {
      try {
        this.fabdom.children[0].style.width = '6.4rem'
      } catch (e) {
        console.error(e)
      }
    }
    const ionsearch = this.element.nativeElement.querySelector('#ion-search')
    if (ionsearch) {
      ionsearch.firstChild.children[2].disabled = true;//搜索框设置为disabled
    }
  

  }

  /**
   * 跳轉到應用頁面, 判断是否为本地页面
   * @param widget 应用对象
   */
  openWidget(widget: Widget) { 
      let page: Webpage = {
        url:  widget.appPageName || widget.homeUrl,  // 链接地址
        title: widget.widgetName,  // 标题
        description: "",   // 摘要内容
        imageUrl: widget.iconUrl,   // 图片路径
        appId: widget.id.toString(), // 應用ID
        options: {
          canShare:  widget.canShare,
          allowCopyLink:  widget.allowCopyLink,
          allowOpenInBrowser: widget.allowOpenInBrowser,
          useNativeBrowser: widget.useNativeBrowser,
        }
       
      };
      this.widgetProvider.openWebpage(page, this.navCtrl); 
  }

  /**
   * 开启轮播图, 判断是否为本地页面
   * @param widget 应用对象
   */
  openSlide(widget: any) { 
      let page: Webpage = {
        url: widget.redirectUrl,  // 链接地址
        title: widget.title,  // 标题
        description: "",   // 摘要内容
        imageUrl: widget.bannerUrl,   // 图片路径
        appId: "" ,// 應用ID, 
        options: {
          canShare:  widget.canShare,
          allowCopyLink:  widget.allowCopyLink,
          allowOpenInBrowser: widget.allowOpenInBrowser,
          useNativeBrowser: widget.useNativeBrowser,
        }
      };
      this.widgetProvider.openWebpage(page, this.navCtrl); 
  }

  /**
   * 开启友情链接
   * @param widget widget 应用对象
   */
  openFavorites(widget: any) { 
      let page: Webpage = {
        url: widget.redirectUrl,  // 链接地址
        title: widget.title,  // 标题
        description: "",   // 摘要内容
        imageUrl: widget.bannerUrl,   // 图片路径
        appId: "" ,// 應用ID, 
        options: {
          canShare:  widget.canShare,
          allowCopyLink:  widget.allowCopyLink,
          allowOpenInBrowser: widget.allowOpenInBrowser,
          useNativeBrowser: widget.useNativeBrowser,
        }
      };
      this.widgetProvider.openWebpage(page, this.navCtrl); 
  }


  openHotNews(widget: any){
    let page: Webpage = {
      url: widget.redirectUrl,  // 链接地址
      title: widget.title,  // 标题
      description: "",   // 摘要内容
      imageUrl: widget.bannerUrl,   // 图片路径
      appId: "" ,// 應用ID, 
      options: {
        canShare:  widget.canShare,
        allowCopyLink:  widget.allowCopyLink,
        allowOpenInBrowser: widget.allowOpenInBrowser,
        useNativeBrowser: widget.useNativeBrowser,
      }
    };
    this.widgetProvider.openWebpage(page, this.navCtrl); 
  }
 

  //进入扫描页面
  openScanPage() {
    this.openAppPage('ScanPage')
  }

  // 跳轉到全部應用Tab頁面
  gotoTabWidgets() {
    this.tabRef.selectTab(2);
  }

  //进入搜索页面
  openSearchPage(event) {
    event.stopPropagation();
    this.openAppPage('SearchPage')
  }

  /**
   * 打开干部招聘页面
   * @param item 
   */
  openJobDetails(item) {
    this.openAppPage('IdlRecruitmentPage', item)
  }

  /**
   * 打开身份绑定
   * @param item 
   */
  openBindEmployee(item) {
    this.openAppPage('EmployeeIdentityPage', item)
  }

  /**
   * 打开页面
   * @param widget 页面id
   * @param item 参数
   */
  openAppPage(widget, item?) {
    if (widget) {
      this.navCtrl.push(widget, item)
    }
  }

  //打开客服
  openFeedback() { 
    this.widgetProvider.openFeedback(this.navCtrl);
  }

  //格式化坐标，控制按钮不能超出界面
  formatCoords() {
    let height = window.screen.availHeight;
    let width = window.screen.availWidth;

    if (this.touchTarget.initCoords.x < 0) {
      this.touchTarget.initCoords.x = 0;
    } else if (this.touchTarget.initCoords.x > width - 56) { //56按钮宽度  
      this.touchTarget.initCoords.x = width - 56;
    }
    if (this.touchTarget.initCoords.y < 0) {
      this.touchTarget.initCoords.y = 0;
    } else if (this.touchTarget.initCoords.y > height - 56 - 62 - 56) { //56按钮高度，62tab页选项卡高度，56头部高度
      this.touchTarget.initCoords.y = height - 56 - 62 - 56
    }

    if (!this.fabdom) this.fabdom = this.element.nativeElement.querySelector('#homemainfab');
    try {
      this.fabdom.style.left = this.touchTarget.initCoords.x + 'px';
      this.fabdom.style.top = this.touchTarget.initCoords.y + 'px';
    } catch (e) {
      console.error(e);
    }
    this.srcollable = true;
  }

  @HostListener('touchmove', ['$event'])//监听滑动事件，阻止默认行为（IOS）
  private onTouchMoveEvent(event: Event): void {
    if (this.nativeService.isIos()) {
      if (!this.srcollable) {
        event.preventDefault();
      }
    }
  }


  initDragulaService() {
    //变更设置通知
    this.events.subscribe('setting:changed', (value) => {
      this.isShowFabButton = value;
      this.storageService.setItem("isShowFabButton", value);
    });

    //查找dragulaService是否存在uniqueBagId，若有，则destory
    const bag: any = this.dragulaService.find(this.uniqueBagId);
    if (bag !== undefined) {
      this.dragulaService.destroy(this.uniqueBagId);
    }

    //配置dragulaService
    this.dragulaService.setOptions(this.uniqueBagId, { copy: true });

    this.dragulaService.drag.subscribe((value) => { //记录拖动起点坐标 
      this.srcollable = false;
      this.touchTarget.touchBefore.x = Utility.getCurrentTouchCoords().x;
      this.touchTarget.touchBefore.y = Utility.getCurrentTouchCoords().y;
    }, (error) => {
      console.error(error)
    });

    this.dragulaService.dragend.subscribe((value) => {  //拖动完毕处理
      this.srcollable = true;
      this.touchTarget.touchAfter.x = Utility.getCurrentTouchCoords().x;
      this.touchTarget.touchAfter.y = Utility.getCurrentTouchCoords().y;

      this.touchTarget.initCoords.x += this.touchTarget.touchAfter.x - this.touchTarget.touchBefore.x;
      this.touchTarget.initCoords.y += this.touchTarget.touchAfter.y - this.touchTarget.touchBefore.y;

      this.formatCoords();

      this.storageService.setItem("touch-coords", this.touchTarget.initCoords);
    })
  }

  //获取历史坐标
  async getHistoryLocation() {
    try {
      const touchCoords = await this.storageService.getItem("touch-coords");
      //获取设置是否启用悬浮球
      const isShowFabButton = await this.storageService.getItem("isShowFabButton");
      if (touchCoords) this.touchTarget.initCoords = touchCoords;
      if (isShowFabButton === false) {
        this.isShowFabButton = false;
      } else {
        this.isShowFabButton = true;
      }
      this.formatCoords();
    } catch (err) {
      this.logger.log(JSON.stringify(err), 'this.storageService.getItem("touch-coords")')
    }

  }



  async initHomeData() {
    if (this.nativeService.isConnecting()) {
      try {
        const homedata = await this.storageService.getItem('homedata');
        const isPunchMember = await this.storageService.getItem('isPunchMember');
        const isEmployee = await this.storageService.getItem('isEmployee');


        if (homedata) {
          this.friendlyLinkList = homedata.friendlyLinks;
          this.hotNews = homedata.hotNews;
          this.cardItems = homedata.slides;
          this.favoriteWidgets = homedata.widgets;
        }
        if (typeof (isPunchMember) === 'boolean') {
          this.sign.isNeedSign = isPunchMember
        }
        if (isEmployee) {
          this.isEmployee = isEmployee
        }
         
      } catch (err) {
        this.logger.log(JSON.stringify(err), 'this.storageService.getItem("homedata")')
      }

    } 
  }

  /**
   * 判断widgets是否含有 IdlRecruitmentListPage 的页面，有则加载职位列表
   * @param widgets 
   */
  initPostList(widgets) {
    this.previewJobs = []
    for (let item of widgets) {
      if (item.homeUrl === 'IdlRecruitmentListPage') {
        this.loadPostList();
        break;
      }
    }
  }

  /**
   * 加载职位列表
   */
  loadPostList() {
    this.idlrecruitingProvider.PostList(null, null, 0, 5, true).subscribe((data) => {
      if (data.errcode == 0) {
        for (let item of data.result) {
          item.distributedOn = Utility.formatTime(this.session.language, item.distributedOn, 'YYYY-MM-DD')
        }
        this.previewJobs = data.result;
      }
    });
  }

  /**
   * 加载业界新闻
   */
  loadNewsList(){
    if(this.isLoadingNews)return;
    this.isLoadingNews = true;
    this.contentProvider.getNewsList(45313411872769, 0 ,10 ,true).subscribe(res => {
        if(res.result){
          for (let item of res.result.newsItems){
            item.publishTime = Utility.formatMessageTime(this.session.language, item.publishTime, 'YYYY-MM-DD') 
          }
          if (!Utility.isEqualsObject(this.newsList, res.result.newsItems)) { 
            // console.log('diff') 
            this.newsList = res.result.newsItems;
          }else {
           // console.log('equals')
          }
        } 
      },err=>{
        
      },() => {
        this.isLoadingNews = false;
      })
  }

  openMoreHotNews(){
    let title = '' 
    for(let widget of this.favoriteWidgets){
      if (widget.id == 59907583691776){
        title = widget.widgetName;
        break;
      }
    }

    this.navCtrl.push('NewsListPage',{
      canShare : true,
      subjectId :   44877979147205,
      title: title
    })
  }
  openNewsList(){  

    let _widget = null;
    for(let widget of this.favoriteWidgets){
      if (widget.id == 59907583691776){
        _widget = widget;
        break;
      }
    }
      let page: Webpage = {
        url: _widget.homeUrl,  // 链接地址
        title: _widget.widgetName,  // 标题
        description: "",   // 摘要内容
        imageUrl: _widget.iconUrl,   // 图片路径
        appId: _widget.id.toString(), // 應用ID 
        options: {
          canShare:  _widget.canShare,
          allowCopyLink:  _widget.allowCopyLink,
          allowOpenInBrowser: _widget.allowOpenInBrowser,
          useNativeBrowser: _widget.useNativeBrowser,
        }
      };
      this.widgetProvider.openWebpage(page, this.navCtrl);  
  }
  /**打开新闻详情 */
  openNewsDetails(item){
    let canShare = false;
    for(let widget of this.favoriteWidgets){
      if (widget.id == 59907583691776){
        canShare = widget.canShare;
        break;
      }
    }
    let page: Webpage = {
      url: item.url,  // 链接地址
      title: item.title,  // 标题
      description: item.title,   // 摘要内容
      imageUrl: item.bannerUrl,   // 图片路径
      appId: '59907583691776', // 應用ID
      options: {
        canShare:  canShare,
        allowCopyLink:  item.allowCopyLink,
        allowOpenInBrowser: item.allowOpenInBrowser,
        useNativeBrowser: item.useNativeBrowser,
      }
    };

    
    this.widgetProvider.openWebpage(page, this.navCtrl);
  }
  initVariable() {
    this.touchTarget = {
      initCoords: {
        x: this.initX,
        y: this.initY
      },
      touchBefore: {
        x: 0,
        y: 0
      },
      touchAfter: {
        x: 0,
        y: 0
      }
    }
    this.cardItems = [
      // {
      //   bannerUrl: '',
      //   title: '',
      //   redirectUrl: ''
      // }
    ];
    this.favoriteWidgets = [];
    this.friendlyLinkList = [];
    this.sign = {
      isNeedSign: null,
      lastSignTime: '',
    }
  }

  /**
   * 开始自动播放轮播图
   */
  startAutoplay() { 
    if (!this.slides) return;
    try {
      this.slides.startAutoplay()
    } catch (e) {
      this.slides.stopAutoplay();
      this.slideTimer = setTimeout(() => {
        this.slides.startAutoplay()
      }, 1000)
    }
  }

  /**
   * 轮播图切换时，控制自动播放
   */
  slideChanged() {
    clearTimeout(this.slideTimer);
    if (this.slides.isEnd()) {
      this.slideTimer = setTimeout(() => {
        this.slides.slideTo(0)
        this.startAutoplay()
      }, this.autoplay)
    } else if (this.slides.isBeginning()) {
      this.slideTimer = setTimeout(() => {
        this.slides.slideNext()
        this.startAutoplay()
      }, this.autoplay)
    } else {
      this.startAutoplay()
    }
  }

  /**
   * 页面进入时，开始轮播图
   */
  ionViewWillEnter() {
    this.slideTimer = setTimeout(() => {
      this.startAutoplay()
    }, 1000)
  }

  /**
   * 页面离开时，停止自动播放轮播图
   */
  ionViewWillLeave() { 
    if (this.slides) {
      this.slides.stopAutoplay()
    }
  }

  resizeSlides(){
    if(this.slides && this.cardItems && this.cardItems.length > 0){
      setTimeout(()=>{
        this.slides.update();
        this.slides.resize();
        this.slides.slideTo(0)
      },1000)
   
    } 
  }
  
  scrollEvent(e) { 
    this.setHeaderBackground(e.scrollTop)
  }

  setHeaderBackground(scrollTop){
    let opacity = scrollTop  / 70

    // console.log(scrollTop)
    // if(scrollTop > 100)return; 
    let header_color = 255;
    let default_icon_color = [170, 242]; //icon颜色区间
    let default_search_input = [211, 255]; //搜索输入框背景颜色区间
    default_search_input[1] = 242 - scrollTop;
    default_icon_color[1] =  242 - scrollTop;
    if(this.session.isDarkMode){
      header_color = 0;
      default_icon_color = [220, 242]; 
      default_search_input = [220, 242]; 
    } 
    this.header.nativeElement.style.backgroundColor = `rgba(${header_color} ,${header_color}, ${header_color},${opacity})` ;
  
    if(default_search_input[1] < default_search_input[0])default_search_input[1] = default_search_input[0]
    if(default_icon_color[1] < default_icon_color[0])default_icon_color[1] = default_icon_color[0]
    const scan = this.element.nativeElement.querySelector('#scan')
    const search = this.element.nativeElement.querySelector('#ion-search')
    if (scan) {
      scan.style.color = `rgb(${default_icon_color[1]} ,${default_icon_color[1]}, ${default_icon_color[1]})`;//设置扫码图标颜色
    }
    if (search) {
      search.children[0].style.backgroundColor = `rgb(${default_search_input[1]} ,${default_search_input[1]}, ${default_search_input[1]},1)`;//设置搜索输入框背景颜色
    }


  }
 

  
   
}



